footer {
 background-color: $mainColor;
 padding: 17px 0;

 .footer-content {
  flex-wrap: wrap;
  .copyright {
   font-size: 14px;
   line-height: 30px;
   margin: 0;
  }

  .socialmedia-list {
   .socialmedia-item {
    margin-#{$end}: 16px;

    .socialmedia-link {
     display: block;
     border: 2px solid #fff;
     width: 28.5px;
     height: 28.5px;
     line-height: 22.5px;
     border-radius: 50%;
     text-align: center;

     i {
      font-size: 9px;
     }
    }
   }
  }
 }
}

@media (max-width: 400px) {
 .footer-content {
  .copyright {
   margin-bottom: 8px !important;
  }

  justify-content: center !important;
  align-self: center !important;
 }
}
