*,
*:before,
*:after {
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

html {
 scroll-behavior: smooth;
}

h2 {
 font-size: 50px;
 font-weight: bold;
}

ul {
 list-style: none !important;
 margin: 0 !important;
 padding: 0 !important;
}

a {
 text-decoration: none;
 margin: 0 !important;
 padding: 0 !important;
}

button {
 border: none;
 outline: none;
}

.section-pd {
 padding: 90px 0;
}

.section-title {
 margin-bottom: 65px;
 h2 {
  margin-bottom: 35px;
 }

 .section-title__image img {
  width: 246px;
 }
}

.section-btn {
 display: inline-block;
 padding: 18px 48px !important;
 font-size: 22px;
 background-color: $mainColor;
 color: #fff;

 &:hover {
  color: #fff;
 }
}
