.new-items {
 background-image: url("../images/backgrounds/new.jpg");
 background-repeat: no-repeat;
 background-size: cover;
 position: relative;

 &::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba($color: #000000, $alpha: 0.8);
  z-index: 0;
 }

 .section-content,
 .section-title {
  position: relative;
  z-index: 1;
 }

 .new-item {
  padding: 0 18px;
  .card {
   border-radius: 8px;
   padding-bottom: 22px;
   .card-img-top {
    padding: 8px 6px;
   }

   .card-body {
    .card-title {
     color: $darkTextColor;
     font-size: 25px;
     margin-bottom: 16px;
    }

    .line {
     width: 100%;
     height: 2px;
     background-color: $mainColor;
    }

    .card-text {
     font-size: 14px;
     color: $lightTextColor;
     line-height: $lineHeight;
    }

    .item-details {
     flex-wrap: wrap;
     a {
      color: #fff;
      background-color: $mainColor;
      font-size: 15px;
      display: block;
      padding: 8px 25px !important;
     }

     .item-price {
      margin: 0 !important;
      color: $mainColor;
      font-size: 20px;
      font-weight: 700;
      line-height: 38px;

      span:nth-child(2) {
         margin-left: 6px;
      }
     }
    }
   }
  }
 }

 .section-btn-wrapper {
  text-align: center;
  margin-top: 50px;
 }
}
