/* Font for LTR */
@font-face {
 font-family: "Cairo";
 src: url("../fonts/cairo/Cairo-Regular.eot"); /* IE9 Compat Modes */
 src: url("../fonts/cairo/Cairo-Regular.eot?#iefix") format("embedded-opentype"),
  /* IE6-IE8 */ url("../fonts/cairo/Cairo-Regular.woff2") format("woff2"),
  /* Super Modern Browsers */ url("../fonts/cairo/Cairo-Regular.woff")
   format("woff"),
  /* Pretty Modern Browsers */ url("../fonts/cairo/Cairo-Regular.ttf")
   format("truetype"),
  /* Safari, Android, iOS */ url("../fonts/cairo/Cairo-Regular.svg#svgFontName")
   format("svg"); /* Legacy iOS */
}

body {
 direction: rtl;
 font-family: "Cairo";
}

@import "./sass/directions/rtl-direction";
@import "./sass/helpers/variables";
@import "./sass/helpers/comman-styles";
@import "./sass/components/home";
@import "./sass/components/about-us";
@import "./sass/components/new-items";
@import "./sass/components/contact-us";
@import "./sass/components/footer";
