header {
 .header-top {
  background-color: $mainColor;
  padding: 13.5px 0;
 }

 .header-top a {
  color: #fff;
 }

 .contact-details {
  .contact-details__list-item:first-child {
   margin-left: 80px;
  }
 }

 .socialmedia {
  .socialmedia__list-item {
   width: 30px;
   height: 30px;
   line-height: 30px;
   border: 1px solid #fff;
   border-radius: 50%;
   text-align: center;
   margin-#{$end}: 8px;
  }
 }

 .navbar {
  .nav-item:not(:last-child) {
   margin-#{$end}: 68px;
  }

  .nav-link {
   font-size: 18px;
   transition: $transition;

   &.active,
   &:hover {
    color: $mainColor !important;
   }
  }

  .navbar-nav {
   padding: 0 30px !important;
  }
 }
}

.banner {
 height: 570px;
 position: relative;

 .slider {
  height: 100%;

  .slider-item {
   height: 570px;
   width: 100%;
   position: relative;
   background-repeat: no-repeat;
   background-size: cover;

   &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
   }
  }

  .slider-item:nth-child(1) {
   background-image: url("../images/backgrounds/new.jpg");
  }
  .slider-item:nth-child(2) {
   background-image: url("../images/backgrounds/new.jpg");
  }
  .slider-item:nth-child(3) {
   background-image: url("../images/backgrounds/new.jpg");
  }

  .slider-item__content {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   z-index: 99999;

   h1 {
    color: $mainColor;
    font-size: 57px;
    margin-bottom: 25px;
   }

   p {
    color: #fff;
    line-height: 26px;
    margin-bottom: 25px;
   }

   button {
    font-size: 22px;
    background-color: $mainColor;
    color: #fff;
    padding: 14px 44px;
   }
  }
 }

 /************ Edit slider next and prev icons *************/
 .owl-carousel {
  .owl-nav {
   position: absolute;
   background-color: aqua;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   width: 100%;

   span {
    display: none;
   }

   .owl-prev,
   .owl-next {
    position: absolute;
    color: $mainColor;
    width: 39px;
    height: 39px;
    border: 4px solid $mainColor;
    border-radius: 50%;

    &:hover {
     background-color: transparent;
    }
   }

   .owl-next {
    #{$start}: 60px;

    &::before {
     content: "\f054";

     right: 9px;
    }
   }

   .owl-prev {
    #{$end}: 60px;

    &::before {
     content: "\f053";

     left: 9px;
    }
   }

   .owl-next::before,
   .owl-prev::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    font-size: 17px;
    position: absolute;
    top: 3px;
   }
  }
 }
}

@media (max-width: 767px) {
 .header-top {
  display: none;
 }
}

@media (max-width: 500px) {
 .owl-prev,
 .owl-next {
  display: none !important;
 }
}
