.contact-us {
 .section-title {
  h2 {
   font-size: 31px;
   color: $mainColor;
  }
 }

 iframe {
  width: 100%;
 }

 form {
  //   background-color: yellow;
  padding: 0 30px;

  .input-control,
  .textarea-control {
   position: relative;

   label {
    position: absolute;
    top: -20px;
    #{$start}: 0;
    display: block;
    color: rgb(236, 79, 79);
    font-size: 12px;
   }
  }

  .input-control:nth-child(1) {
   margin-left: 32px;
  }
 }

 .input-control {
  display: inline-block;

  input {
   width: 319px;

   &::placeholder,
   textarea::placeholder {
    font-size: 14px;
    color: #58585a;
   }
  }

  input[type="email"] {
   direction: rtl;
  }
 }

 input:not(input[type="submit"]),
 textarea {
  border: 1px solid #979797;
  padding: 16px 0;
  padding-right: 15px;
  margin-bottom: 24px;
 }

 input[type="submit"] {
  border: none;
 }

 textarea {
  width: 100%;
  resize: none;
  height: 165px;
 }
}

@media (max-width: 991px) {
 .input-control {
  width: 100%;
  input {
   width: 100% !important;
  }
 }
}
